html {
  font-size: 62.5%;
}

body {
  font-family: "Helvetica Neue","Helvetica Neue HQ",Helvetica,Arial,sans-serif;
  font-size: 1.6rem;
}

.header {
  margin: 1.5rem;
}

.header, .header__form {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.form__box {
  margin: 1.3rem 2rem 0.5rem 2rem;
}

.form__box:nth-child(2) {
  margin-right: 3rem;
  margin-bottom: 1rem;
}

.header__form label {  
  margin-right: 1rem;
}

.header__form input {
  border-color: black;
  background-color: white;
  border-width: 0 0 2px 0;
  border-style: solid;
  width: 20rem;
  height: 2.8rem;
  padding: 0.1rem;
}

.header__form input:focus, .header__form button:focus {
  outline-width: 0;
}

.header__form button {
  border: 2px solid black;
  background-color: white;
  width: 20rem;
  height: 3.2rem;
  cursor: pointer;
}

.header__form button:disabled {
  border: 2px solid lightgray;
  color: lightgray;
  background-color: white;
  cursor: not-allowed;
}

.header__form button:hover:not([disabled]) {
  background-color: black;
  color: white;
}

.title__logo {
  -webkit-animation-name: rotate;
          animation-name: rotate;
  -webkit-animation-duration: 2s;
          animation-duration: 2s; 
  -webkit-animation-iteration-count: infinite; 
          animation-iteration-count: infinite;
}
  
@-webkit-keyframes rotate {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
  
@keyframes rotate {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
  
.kpis {
    margin: 1.5rem;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .kpis__box {
    text-align: center;
    margin: 1rem 2.5rem 0 2.5rem;
  }
  
  .kpis__label {
    font-size: 1.3em;
  }
  
  .kpis__value {
    font-size: 1.5em;
  }
  
footer {
  margin: 3rem 1.5rem 1.5rem 1.5rem;
  font-size: 1.2rem;
  text-align: center;
}
